import React from 'react';

export default (
  <div>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./como é feito o calculo</h3>
    </div>
    <p>
      No meiado de 2014 comecei a ficar interressado em saber exatamente aonde meu tempo é gasto, desse interesse surgiu a idea de criar uma <a target="_blank" rel="noopener noreferrer" href="https://0000marcell.github.io/p36-hours/#/clock">app</a> para coletar esses dados, o objetivo no inicio era totalmente pessoal, eu queria saber aonde eu estava gastando meu tempo pra poder focar nas coisas que eu julgasse mais importantes, depois de alguns anos usando esse sistema surgiu a idea de também usa-lo para ser mais transparente nas minhas habilidades, e isso serve tanto para quem me contrata quanto para mim, a transparencia desse sistema também me motiva a investir mais tempo em areas importantes para meu trabalho, o sistema foi usado mais para meu trabalho nesses anos, mas recentemente resolvi expandir para outras areas, os dados no app são organizados por subtarefas e tags o que torna bem facil extrai-los e gerar visualizações, visitando a app da pra ter uma boa idea de como isso é feito, clicando no botão "?" você tem acesso a uma tour que explica como a app funciona.</p> 
  <p>é importante mencionar que o tempo gasto é especificamente escrevendo código ou estudando e não o tempo gasto em um projeto que envolve determinada tecnologia, o tempo gasto em um projeto envolve varias outras coisas, escrever código e só uma parte, outra coisa importante e que existe overlap entre os dados, parte do tempo gasto em javascript por exemplo também conta para react, dependendo da tag que a tarefa tem e das subtarefas.</p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./pontos fracos do sistema</h3>
    </div>
    <p>
      Um dos pontos fracos do sistema é não avaliar qualidade, avaliar qualidade é muito dificil de qualquer maneira, e a quantidade de horas em determinada tarefa não necessariamente significa fazendo algo significativo, que irá avançar minha capacidade na determinada habilidade, porém comparativamente, serve para saber a distribuição do meu tempo em cada habilidade pra me guiar em direção a coisas que fazem mais sentido pra mim, no geral o tempo támbem é um indicador razoável da capacidade em cada coisa, não serve como comparativo entre pessoas distintas, mas como informação pessoal e de forma generalista é valioso.
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./sobre a distribuição das habilidades em grupos</h3>
    </div>  
    <p>
      Tive a idéia de distribuir as habilidades em grupos diferentes refentemente, por isso a maior parte do tempo está em desenvolvimento, em parte por causa disso e em parte por que essa é realmente a aréa onde gasto a maior parte do meu tempo, predento gastar mais tempo desenvolvendos outros grupos de habilidades, por isso reoganizei meu calendario e fiz algumas projeções, dividir as habilidades em grupos me mostra onde gastar meu tempo. 
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./sobre as projeções</h3>
    </div>
    <p>
      As projeções são feitas inteiramente no calendário do proximo ano, esse calendário pode mudar mas eu pretendo pelo menos 
      chagar perto dos resultados da projeção.
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./Idéias e sugestões</h3>
    </div>
    <p>
      Idéias e sugestões são sempre bem vindas, eu pretendo mudar o sistema de hóras para alguma coisa mais significativa no futuro, e incluir uma parte especifica para desáfios, projetos feitos especificamente para melhorar alguma habilidade.
    </p>
  </div>
);



